<template>
    <div id="personal-center">
      <div>
        <div class="title clearfix">
            <span class="header left"></span>
            <router-link :to='{path:"/personalcenter/userinfoeditor"}'>
                <span class="edit right"></span>
            </router-link>
        </div>
        <div class="base-userinfo">
            <div class="info-item clearfix phone">
              <div class="info-title left"></div>
              <div class="info-content left">
                {{phone}}
              </div>
            </div>
            <div class="info-item clearfix name">
              <div class="info-title left"></div>
              <div class="info-content left">
                {{name}}
              </div>
            </div>
            <div class="info-item clearfix sex">
              <div class="info-title left"></div>
              <div class="info-content left">
                {{sex}}
              </div>
            </div>
            <div class="info-item clearfix identity">
              <div class="info-title left"></div>
              <div class="info-content left">
                {{identity}}
              </div>
            </div>
            <div class="info-item clearfix health">
              <div class="info-title left"></div>
              <div class="info-content left">
                <img v-if="healthImg === ''" src="../images/symbols-empty-pic_pic_normal.png">
                <img v-else :src="imgbase_url + '/img_get.php?img=1&height=200&width=120&imgname=' + healthImg">
              </div>
            </div>
            <div class="info-item clearfix wx">
              <div class="info-title left"></div>
              <div class="info-content left">
                {{wexin}}
              </div>
            </div>
        </div>

        <div class="create-content" v-if="isShowcreatebtn">
          <div class="create-btn" @click="openCreateShop">
            
          </div>
        </div>
      </div>

      <div class="show-work">
        <div class="title clearfix" v-if="isShowworkinfo">
            <span class="header left"></span>
        </div>
        <div class="work-content" v-if="isShowworkinfo">
            <div class="info-item clearfix shop">
              <div class="info-title left"></div>
              <div class="info-content left">
                {{shop_name}}
              </div>
            </div>
            <div class="info-item clearfix position">
              <div class="info-title left"></div>
              <div class="info-content left">
                {{position_name}}
              </div>
            </div>
            <div class="info-item clearfix department">
              <div class="info-title left"></div>
              <div class="info-content left">
                {{department_name}}
              </div>
            </div>
        </div>
      </div>
      
      <!-- // -->
      <create-shop :dialogVisible="dialogVisible" @on-close="closeCreateShop" @creatshop-sucess="creatshopSuc"></create-shop>
    </div>
</template>

<script>
import { getUserinfo } from "../api";
import { Public } from "@/config/public";
import { isBindWexin, CustomSex } from "@/config/cfg";

import CreateShop from "./creatShop";

export default {
  components: {
    CreateShop
  },
  data() {
    return {
      phone: "",
      name: "",
      sex: "",
      identity: null,
      healthImg: "",
      wexin: "",

      //
      isShowworkinfo: true,
      department_name: "",
      position_name: "",
      shop_name: "",

      //
      isShowcreatebtn: true,

      base_url: base_url,
      imgbase_url: imgbase_url,

      dialogVisible: false //
    };
  },
  created() {
    this.initCreatedBtn();
    this.init();
  },
  methods: {
    init() {
      let data = {
        get_user_info: 1
      };

      getUserinfo(data, resp => {
        if (resp.ret === 0) {
          let userinfo = resp.data.userinfo || {};
          let workinfo = resp.data.work_info_list || [];

          this.phone = userinfo.phone;
          this.name = userinfo.real_name;
          this.sex = CustomSex.toString(userinfo.sex);
          this.identity = userinfo.identity;
          this.healthImg = userinfo.health_certificate || "";
          this.wexin = isBindWexin.toString(userinfo.is_weixin);

          if (workinfo.length === 0) {
            this.isShowworkinfo = false;
          } else {
            this.shop_name = workinfo[0].shop_name;
            this.position_name = workinfo[0].position_name;
            this.department_name = workinfo[0].department_name;
          }
        } else {
          console.warn("");
        }
      });
    },
    initCreatedBtn() {
      let shopinfo = Public.getShopinfo().shopinfo || [];

      if (shopinfo.length !== 0) {
        // 
        this.isShowcreatebtn = false;
      }
    },

    openCreateShop() {
      this.dialogVisible = true;
    },
    closeCreateShop() {
      this.dialogVisible = false;
    },
    creatshopSuc(){
      this.$emit("creatshop-sucess");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#personal-center {
  @include fc(14px, #333);
  background-color: #fff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.title {
  @include fc(14px,#666);
  height: 40px;
  line-height: 40px;
  background: #f6f8fc;
  .header {
    padding-left: 14px;
  }
  .edit {
    cursor: pointer;
    position: relative;
    color: $blue;
    padding-right: 20px;
    &:before {
      position: absolute;
      left: -20px;
      top: 10px;
      content: "";
      @include wh(20px, 20px);
      background: url("../images/edit.png")no-repeat;
    }
  }
}

.info-title {
  width: 220px;
  text-align: right;
  opacity: 0.5708;
  margin-right: 14px;
}

.info-content {
  @include fc(14px, #666);
  min-width: 300px;
  // max-width: 1021px;
  text-align: justify;
  word-break: break-all;
}
.info-item {
  padding-bottom: 35px;
  line-height: 19px;
}
.info-item.phone {
  padding-top: 48px;
}
.info-item.identity {
  padding-bottom: 27px;
}
.info-item.health {
  padding-bottom: 22px;
  .info-content {
    @include wh(200px, 120px);
    img {
      @include wh(200px, 120px);
    }
  }
}
.info-item.wx {
  padding-bottom: 42px;
}
.info-item.wx {
  padding-bottom: 42px;
}
.show-work {
  height: 304px;
  background-color: #fff;
}
.info-item.shop {
  padding-top: 48px;
}
.info-item.department {
  padding-bottom: 89px;
}
.create-content {
  height: 90px;
  position: relative;
}
.create-btn {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  @include wh(160px,40px);
  line-height: 40px;
  text-align: center;
  border-radius: 2px;
  font-size: 14px;
  color: #4877e7;
  display: inline-block;
  border: 1px solid #4877e7;
  cursor: pointer;
}
</style>


